import React, { useState, useEffect } from "react";
import { Descriptions, Badge, Card } from "antd";
import moment from "moment";
import CourseStatChart from "./Chart";
import CourseApi from "api/course-api";

const Overview = (props) => {
  const [item, setItem] = useState(null);
  const { param } = props;

  useEffect(() => {
    const { id } = param;
    const api = new CourseApi();
    api.getCourseDashboard(parseInt(id)).then((response) => {
      setItem(response.data);
    });
  }, [param, props]);

  return (
    <>
      <Card>
        {item && (
          <Descriptions bordered>
            <Descriptions.Item label="Tn" span={2}>
              {item.name}
            </Descriptions.Item>
            <Descriptions.Item label="Danh mc">
              {item.categoryName}
            </Descriptions.Item>
            <Descriptions.Item label="Trng thi">
              {item.status == 2 && (
                <Badge status="processing" text="ang din ra" />
              )}
              {item.status != 2 && <span>{item.statusName}</span>}
            </Descriptions.Item>
            <Descriptions.Item label="Thi gian bt u">
              {item.startDate && (
                <>{moment(item.startDate).format("YYYY-MM-DD HH:mm:ss")}</>
              )}
            </Descriptions.Item>
            <Descriptions.Item label="Thi gian kt thc">
              {item.endDate && (
                <>{moment(item.endDate).format("YYYY-MM-DD HH:mm:ss")}</>
              )}
            </Descriptions.Item>
            <Descriptions.Item label="Hnh thc tham gia">
              {item.registrationTypeName}
            </Descriptions.Item>
            <Descriptions.Item label="S hc vin">
              {item.totalUser}
            </Descriptions.Item>
            <Descriptions.Item label="S lt hc">
              {item.totalAttempt}
            </Descriptions.Item>
            <Descriptions.Item label="Lch s" span={3}>
              Ngy to: {moment(item.createdTime).format("YYYY-MM-DD HH:mm:ss")}
              <br />
              Ngy sa gn nht:{" "}
              {moment(item.lastModifiedTime).format("YYYY-MM-DD HH:mm:ss")}
            </Descriptions.Item>
          </Descriptions>
        )}
      </Card>
      <br />
      <Card title="Thng k">
        <CourseStatChart />
      </Card>
    </>
  );
};

export default Overview;
